* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
      Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  }

  ul {
    list-style-type: none;
    padding-left: 0;
  }
  
  .main-container {
    padding: 12px;
    padding-top: 5rem;
    
  }

  .show-transition{
    opacity: 0;
    transform: translateX(-10px);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }

  .show-transition.show {
    opacity: 1;
    transform: translateX(0);
  }
  

  /* HEADING */

  .heading {
    text-align: center;
  }

  .heading__title {
    font-weight: 600;
  }

  .heading__credits {
    margin: 10px 0px;
    color: #888888;
    font-size: 25px;
    transition: all 0.5s;
  }

  .heading__link {
    text-decoration: none;
    cursor: pointer;
  }

  .vertical_align_middle {
    vertical-align: middle;
  }

  .heading__credits .heading__link {
    color: inherit;
  }
  
  /* CARDS */

  .cards_ct {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    cursor: pointer;
  }

  .card_ct {
    margin: 6px;
    padding: 6px;
    width: 500px;
    min-height: 200px;
    display: grid;
    /* grid-template-rows: 20px 50px 1fr 1fr 50px; */
    /* grid-template-rows: 20px 50px 1.7rem 1fr 50px; */
    grid-template-rows: auto auto auto auto auto;
    /* gap: 0.5rem; */
    border-radius: 10px;
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.25);
    transition: all 0.2s;
  }

  .card_ct:hover {
    box-shadow: 0px 6px 10px rgba(0, 0, 0, 0.4);
    transform: scale(1.01);
  }

  .card__link,
  .card__fee,
  .card__icon {
    position: relative;
    text-decoration: none;
    color: rgba(255, 255, 255, 0.9);
    cursor: pointer;
  }

  .card__ratings {
    align-items: flex-start;
    display: flex;
    flex-wrap: nowrap;
    list-style: none;
    margin: 1rem 0;
    overflow-x: scroll;
    padding: 0 0 0 1rem;
    /* scroll-padding-left: 1rem; */
    scroll-snap-type: x mandatory;
    /* Hide on Firefox */
    scrollbar-width: none
  }

  /* Hide on WebKit browsers */
  .card__ratings::-webkit-scrollbar {
    display: none;
  }

  .card__link::after {
    position: absolute;
    top: 25px;
    left: 0;
    content: "";
    width: 0%;
    height: 3px;
    background-color: rgba(255, 255, 255, 0.6);
    transition: all 0.5s;
  }

  .card__link:hover::after {
    width: 100%;
  }

  .card__fee {
    grid-row: 1/2;
    justify-self: end;
    font-size: 0.8rem;
    white-space: nowrap;
  }

  .card__icon {
    grid-row: 2/3;
    font-size: 1.3rem;
    font-weight: 300;
    text-transform: capitalize;
  }

  .card__title {
    grid-row: 3/4;
    font-weight: 500;
    color: #ffffff;
    font-size: 1.3rem;
    text-transform: capitalize;
  }

  .card__ratings {
    grid-row: 4/5;
    align-self: center;
  }

  .ratings{
    border-radius: 12px;
    background-color: lightgoldenrodyellow;
    font-size: 0.8rem;
    padding: 5px;
    list-style-type: none;
    /* float: left; */
    margin: 5px;
    color: black;
    /* font-weight: 400; */
    /* padding: 0 1rem 0 0; */
    scroll-snap-align: start;
    scroll-snap-stop: always;
    white-space: nowrap;
    cursor: pointer;
  }

  .card__apply {
    grid-row: 5/6;
    align-self: center;
  }

